html {
  font-size: 62.5%;
  --theme-width: 450px;
  --theme-text_0: hsl(0deg 0% 20%);
  --theme-text_1: hsl(0deg 0% 43%);
  --theme-bg_0: hsl(27deg 39% 95%);
  --theme-bg_1: hsl(0deg 0% 100%);
  --theme-primary: hsl(266deg 100% 61%);
  --theme-primary_smooth: hsl(266deg 100% 92%);
  --theme-secondary: hsl(27deg 100% 56%);
  --theme-focus-shadow: var(--theme-bg_0) 0 0 0 2px, var(--theme-secondary) 0 0 0 4px;
}

body {
  background-color: var(--theme-bg_0);
  color: var(--theme-text_0);
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  box-sizing: border-box;
  color: #343434;
  line-height: 1.5;
  padding: 45px 16px 90px; /* for fixed footer */
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body * {
  box-sizing: inherit;
}

p {
  margin: 0;
}

// ===== Global Theme ==== //
.g-hero {
  text-align: center;
  margin-bottom: 32px;
  margin-bottom: 60px;

  h1 {
    font-size: 2.1rem;
    margin: 0;
  }

  p {
    color: var(--theme-text_1);
    font-size: 1.4rem;
  }
}

.g-card {
  position: relative;
  max-width: var(--theme-width);
  padding: 32px 16px;
  margin: 24px auto 60px;
  border: 1px solid var(--theme-primary);
  border-radius: 4px;
  background-color: var(--theme-bg_1);
  box-shadow: 2px 2px var(--theme-primary_smooth);
  min-height: 100px;
}

.g-cardTitle {
  position: absolute;
  top: -30px;
  left: 0;

  &-firstWord {
    text-transform: uppercase;
  }
  font-size: 1.6rem;
  margin: 0;
  font-weight: 600;
}

.g-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 24px 16px;
  font-size: 1.4rem;
  text-align: center;
  background: var(--theme-bg_1);
}

.u-link {
  --linkClr: var(--theme-primary);
  position: relative;
  text-decoration: underline;
  -webkit-text-decoration-color: var(--linkClr);
  text-decoration-color: var(--linkClr);
  color: inherit;
  z-index: 0;
  white-space: nowrap;

  &::before {
    content: "";
    position: absolute;
    bottom: 0.05em;
    left: -0.1em;
    width: calc(100% + 0.2em);
    height: 1.2em;
    background-color: var(--linkClr);
    border-radius: 3px;
    opacity: 0.2;
    transform: scale(1, 0.2);
    transform-origin: 0 95%;
    z-index: -1;
    transition: transform 175ms ease-out;
  }

  &:hover::before,
  &:focus::before {
    transition-duration: 350ms ease-in-out;
    transform: scale(1, 1);
  }

  &:focus {
    outline: none;
  }
}

.u-link:hover {
  outline: none;
}


/* screen reader only */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
